import React from 'react';

import Picture1 from '../../../resources/profile.jpeg';
import Picture2 from '../../../resources/Ryu_resized1.jpg';
import Picture3 from '../../../resources/Professional_Picture_resized1.jpg';

import './index.css';

const Profile = () => {
	return (
		<div className='profile'>
			<div className='profile-bg'></div>
			<div className='profile-picture1' style={{ backgroundImage: 'url(\'' + Picture1 + '\')' }}>
				<div className='profile-picture2' style={{ backgroundImage: 'url(\'' + Picture2 + '\')' }}></div>
				<div className='profile-picture3' style={{ backgroundImage: 'url(\'' + Picture3 + '\')' }}></div>
				<div class="penguin">
            <div class="penguin-bottom">
              <div class="right-hand"></div>
              <div class="left-hand"></div>
              <div class="right-feet"></div>
              <div class="left-feet"></div>
            </div>
            <div class="penguin-top">
              <div class="right-cheek"></div>
              <div class="left-cheek"></div>
              <div class="belly"></div>
              <div class="right-eye">
                <div class="sparkle"></div>
              </div>
              <div class="left-eye">
                <div class="sparkle"></div>
              </div>
              <div class="blush-right"></div>
              <div class="blush-left"></div>
              <div class="beak-top"></div>
              <div class="beak-bottom"></div>
            </div>
          </div>			
			</div>


		</div>
	);
}

export default Profile;